.pagination {
  display: flex;
  flex-direction: row;
  /* background-color: rgba(0, 0, 0, 0.028); */
  border-radius: 10px;
  justify-content: center;
  max-width: 620px;
  min-width: 220px;
  margin: auto;
  margin-top: 40px;
  padding: 15px 30px 15px 19px;
}

.pageLink {
  flex: 1;
  align-self: center;
  margin-left: 10px;
  max-height: 40px;
  min-height: 40px;
  max-width: 60px;
  min-width: 60px;
  background-color: rgba(20, 136, 199, 0.035);
  font-weight: bold;
  color: rgb(210, 185, 84);
  font-size: 1.2rem;
  /* padding: 0.5rem 1.5rem; */
  border: 2.4px solid rgba(20, 136, 199, 0.088);
  cursor: var(--cursor-point);
  transition: all 0.2s ease;
  border-radius: 10px;
}

.pageLinkArrow {
  flex: 1;
  color: rgb(2, 118, 242);
  margin-left: 10px;
  max-height: 50px;
  min-height: 50px;
  max-width: 60px;
  min-width: 60px;
  font-weight: bold;
  background-color: rgba(10, 57, 12, 0.166);
  /* background-color: transparent; */
  font-size: 1.5rem;
  padding: 0rem 0rem;
  border: 0px solid rgba(20, 116, 167, 0.795);
  cursor: var(--cursor-point);
  transition: all 0.2s ease;
  /* border-radius: 10px; */
  border-radius: 7px 2px 2px 7px;
}

.pageLinkActive {
  background-color: rgba(20, 136, 199, 0.265);
  /* color: rgba(20, 136, 199); */
}

.pageSelected {
  background-color: black;
}

.tresPuntosPaginate {
  cursor: var(--cursor-point);
  margin-left: 10px;
  color: transparent;
  left: 5px;
  min-width: 20px;
}

@media only screen and (max-width: 649px) {
  .pagination {
    scale: 0.8;
  }
}

@media only screen and (max-width: 455px) {
  .pagination {
    scale: 0.7;
  }
}
@media only screen and (max-width: 455px) {
  .pagination {
    scale: 0.7;
  }
  .pageLink {
    margin: -1px;
  }
}

@media only screen and (max-width: 350px) {
  .pageLinkArrow {
    display: none;
  }
}
