.footer {
  position: inherit;
  bottom: 0;
  margin-top: 530px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: right;
  background-image: linear-gradient(rgba(0, 0, 0, 0.423), rgb(53, 6, 75));
  min-height: 50px;
  width: 100%;
  padding-bottom: 10px;
}

.footerName {
  margin-top: 11px;

  justify-self: center;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  color: rgba(97, 111, 15, 0.789);
}

.footerFindMe {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  color: rgba(192, 220, 36, 0.778);
}
.footerLink {
  margin: 0px 12px 0px 12px;
  flex-direction: column;
}
.footerLink:hover {
  cursor: var(--cursor-point);
  transform: rotate(360deg);
  size: 100px;
}

.footerGit {
  flex-direction: column;
  margin-right: 40px;
}
.footerGit:hover {
  cursor: var(--cursor-point);
  transform: rotate(360deg);
  size: 100px;
}
