.backG {
  background-image: radial-gradient(rgb(193, 35, 35), rgba(8, 8, 122, 0.95));
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  overflow: hidden;
  max-height: 100vh;
  animation: backAnim 1s ease-in-out;
}

@keyframes backAnim {
  0% {
    opacity: 0;
  }
}

.allData {
  position: absolute;
  display: flex;
  flex-direction: column;
  margin-top: -200px;
  justify-content: center;
  align-items: center;
  padding: 0;
  /* animation */
}

.video {
  object-fit: cover;
  width: 100%;
  height: 120vh;
}

.welcome {
  box-shadow: 0px 0px 0px 0px rgba(4, 39, 10, 0.569);
  margin-left: -51px;
  align-items: center;
  margin-right: 3px;
  margin-top: 130px;
  max-width: 180px;
}

.gif {
  margin-top: 30px;
  border-radius: 1000px;
  box-shadow: 1px 1px 20px 7px rgba(4, 39, 10, 0.569);
  max-width: 320px;
  margin-left: -51px;
  cursor: var(--cursor-point);
}

.logo {
  margin-right: 3px;
  margin-top: 20px;
  max-width: 400px;
  margin-left: -51px;
}

.logo:hover {
  transform: blur(10px);
}

@media only screen and (max-width: 445px) {
  .allData {
    max-height: 20px;
  }

  .gif {
    max-width: 70%;
    margin-left: 5px;
  }
  .welcome {
    margin-top: 100px;
    max-width: 40%;
    margin-left: 5px;
  }
  .logo {
    max-width: 70%;
    margin-left: 5px;
  }
}
