@import "./mediaQueryDetails.css";

.loadingPage {
  flex-direction: column;
  align-items: center;
}

.loading {
  margin-top: 20px;
}

.loadingPageModal {
}

.waitingBallModal {
  justify-self: center;
  align-self: center;
  border-radius: 50%;
  max-width: 300px;
  max-height: 300px;
  margin-top: 0px;
  box-shadow: 1px 1px 20px 1px rgb(9, 5, 75);
}

.loadingModal {
  margin-top: 20px;
  width: 20px;
}

.closeButtonModal {
  cursor: var(--cursor-point);
  position: absolute;
  top: 29px;
  right: 33px;
  width: 50px;
  max-width: 50px;
  border-radius: 15px 15px 15px 15px;
}

.closeButtonModal:hover {
  box-shadow: 1px 1px 20px rgba(255, 0, 0, 0.768);
}

.leftArrowModal {
  background-color: rgba(56, 93, 162, 0.214);
  border-radius: 10px;
  cursor: var(--cursor-point);
  position: absolute;
  top: 330px;
  left: 14px;
  height: 60px;
}
.rightArrowModal {
  background-color: rgba(56, 93, 162, 0.214);
  border-radius: 10px;
  cursor: var(--cursor-point);
  position: absolute;
  top: 330px;
  left: 485px;
  height: 60px;
}

.noShow {
  display: none;
}

.leftArrowModal:hover {
  box-shadow: 1px 1px 100px rgba(0, 17, 255, 0.416);
}
.rightArrowModal:hover {
  box-shadow: 1px 1px 100px rgba(0, 34, 255, 0.759);
}

.allDetailCard {
  position: fixed;
  display: flex;
  z-index: 90;
  flex-direction: row;
  background-image: url("../../images/background.jpg");
  /* background-attachment: fixed; */
  background-size: 150%;
  box-shadow: 0.5px 0.5px 20px 10px rgb(9, 5, 75);
  filter: blur(0.4px);
  /* margin-bottom: -200px; */
  border-radius: 20px;
  align-items: center;
  width: 70vw;
  height: auto;
  /* top: 0%; */
  left: 15%;
  animation: allDetailFx 1s ease-in;
}

@keyframes allDetailFx {
  0% {
    height: 10px;
    top: 100%;
    background-size: 10%;
  }
  100% {
    height: 2000px;
    background-size: 150%;
  }
}

@keyframes opacityFx {
  0% {
    opacity: 0;
  }
  70% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.cardArrowContainer {
  position: relative;
  margin: 30px 0px 30px 0px;
  justify-content: center;
  width: 50%;
  animation: opacityFx 1.5s ease-in-out;
}

.card {
  z-index: 0;
  width: 400px;
  height: 600px;
  background-image: url("../../images/backCard.png");
  border-radius: 50px;
  box-shadow: 0.5px 0.5px 20px 0.5px rgb(9, 5, 75);
  margin-left: 80px;
}

.leftArrowModal {
  top: 50%;
}

.rightArrowModal {
  top: 50%;
}

.detailsData {
  position: relative;
  top: 0%;
  bottom: 0%;
  left: 20%;
  animation: opacityFx 1.5s ease-in-out;
}

.cardNotFound {
  position: fixed;
  display: flex;
  z-index: 90;
  flex-direction: row;
  justify-content: center;
  background-image: url("../../images/background.jpg");
  background-attachment: fixed;
  box-shadow: 0.5px 0.5px 20px 10px rgb(9, 5, 75);
  filter: blur(0px);
  margin-top: 35px;
  margin-bottom: -200px;
  border-radius: 5%;
  width: 70vw;
  height: 75vh;
  left: 13vw;
}
.detailPic {
  min-height: 400px;
  max-width: 400px;
  min-width: 200px;
}

.picDataBase {
  margin-bottom: -320px;
}

.waitingBallPic {
  position: relative;
  top: 100px;
  border-radius: 1000px;
  max-width: 220px;
  box-shadow: 1px 1px 20px 1px rgb(9, 5, 75);
  margin-bottom: 20px;
  animation: waitingBallAnim 1s ease-in-out;
}

@keyframes waitingBallAnim {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.detailPicNotFound {
  position: absolute;
  left: 70px;
  top: 140px;
  border-radius: 50px;
  height: 40vh;
  box-shadow: 2px 2px 1rem rgb(187, 3, 77);
}

.detailName {
  text-decoration: dotted;
  text-transform: capitalize;
  text-decoration: none;
  font-size: 1.7rem;
  color: rgb(212, 233, 24);
  margin-top: 20px;
  margin-left: 0px;
  box-sizing: content-box;
  width: 100%;
  transform: rotate(29deg);
}

.parraf {
  display: fixed;
  background-color: rgba(20, 136, 199, 0.835);
  background-size: 90px;
  margin-top: -10px;
  margin-right: 20px;
  font-weight: bold;
  color: rgb(210, 185, 84);
  font-size: 1.3rem;
  padding: 0.5rem 1.5rem;
  border: 2px solid rgba(20, 136, 199, 0.569);
  transition: all 0.3s ease;
  border-radius: 5px;
  max-width: 220px;
  /* min-width: 220px; */
}

.parrafId {
  border-radius: 30px;
  /* box-shadow: -0.1rem 0.5rem 0.1rem rgba(0, 0, 0, 0.467); */
  background-size: 90px;
  font-weight: bold;
  color: rgb(210, 185, 84);
  font-size: 2rem;
  padding: 0;
  margin-top: 50px;
  margin-right: 160px;
  margin-bottom: 12px;
  padding-left: 18px;
}

.idDb {
  font-size: 1rem;
  display: none;
}

.homeBtnDetails {
  background-image: url("../../images/home2.png");
  display: fixed;
  background-color: rgba(20, 136, 199, 0.835);
  background-size: 90px;
  /* margin-top: -100px; */
  margin-right: 20px;
  font-weight: bold;
  color: rgb(210, 185, 84);
  font-size: 1rem;
  padding: 0.5rem 1.5rem;
  border: 2px solid rgba(20, 136, 199, 0.569);
  cursor: var(--cursor-point);
  transition: all 0.3s ease;
  border-radius: 13px;
}

.homeBtnDetails:hover {
  background-color: rgba(20, 136, 199, 0.432);
  box-shadow: 2px 2px 1rem rgb(187, 3, 77), -0.3rem -0.3rem 1rem rgb(31, 7, 149);
  border: 2px solid rgba(20, 136, 199, 0.227);
}

.homeBtnDetails:active {
  box-shadow: 0.3rem 0.3rem 1rem rgb(8, 8, 122),
    -0.3rem -0.3rem 1rem rgb(187, 3, 35);
}

.createBtnDetails {
  background-image: url("../../images/home2.png");
  /* display: fixed; */
  background-color: rgba(20, 136, 199, 0.835);
  background-size: 90px;
  /* margin-top: -100px; */
  /* margin-left: 800px; */
  font-weight: bold;
  color: rgb(210, 185, 84);
  font-size: 1rem;
  padding: 0.5rem 1.5rem;
  border: 2px solid rgba(20, 136, 199, 0.569);
  cursor: var(--cursor-point);
  transition: all 0.3s ease;
  border-radius: 13px;
}

.createBtnDetails:hover {
  background-color: rgba(20, 136, 199, 0.432);
  box-shadow: 2px 2px 1rem rgb(187, 3, 77), -0.3rem -0.3rem 1rem rgb(31, 7, 149);
  border: 2px solid rgba(20, 136, 199, 0.227);
}

.createBtnDetails:active {
  box-shadow: 0.3rem 0.3rem 1rem rgb(8, 8, 122),
    -0.3rem -0.3rem 1rem rgb(187, 3, 35);
}

/* MEDIA QUERIES */

@media only screen and (min-width: 1363px) {
  .allDetailCard {
    top: 11%;
    left: 13%;
  }
  .cardArrowContainer {
    left: 10%;
  }
  .leftArrowModal {
    left: 0%;
  }
  .rightArrowModal {
    /* right: 0%; */
  }
}

@media only screen and (max-width: 1363px) {
  .allDetailCard {
    position: fixed;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    z-index: 90;
    background-attachment: fixed;
    box-shadow: 0.5px 0.5px 20px 10px rgb(9, 5, 75);
    filter: blur(0px);
    margin-top: 35px;
    /* border-radius: 5%; */
    width: 70vw;
    height: auto;
    left: 14vw;
    top: -2%;
    animation: allDetailFx1363 1s ease-in;
  }

  .cardArrowContainer {
    display: grid;
    grid-template-columns: 1% 10% 10%;
    justify-items: center;
    right: 0%;
    scale: 0.9;
  }

  .leftArrowModal {
    position: sticky;
    /* margin-top: 510px; */
    margin-right: 250px;
    margin-bottom: 200px;
  }

  .rightArrowModal {
    position: sticky;
    /* align-items: center; */
    margin-left: 370px;
    margin-bottom: 200px;
  }

  .card {
    align-items: center;
    z-index: 0;
    width: 300px;
    height: 450px;
    border-radius: 50px;
    box-shadow: 0.5px 0.5px 20px 0.5px rgb(9, 5, 75);
  }

  .waitingBallPic {
    margin-left: 10px;
    margin-top: -70px;
    margin-bottom: 160px;
  }

  .detailsData {
    top: -25px;
    margin-right: 30%;
    margin-bottom: 0%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .parrafId {
    margin-top: -10px;
  }

  .parraf {
    /* height: 45px; */
    font-size: 1em;
  }

  .detailPic {
    scale: 0.7;
    margin-left: -40px;
    margin-top: -100px;
  }

  .closeButtonModal {
    scale: 0.8;
  }
}

@keyframes allDetailFx1363 {
  0% {
    height: 10px;
    top: 100%;
    background-size: 50%;
  }
  100% {
    height: 2000px;
    background-size: 150%;
  }
}

