@media only screen and (max-width: 890px) {
  .allDetailCard {
    position: fixed;
    width: 80vw;
    left: 10%;
    height: auto;
    top: -20px;
  }
  .cardArrowContainer {
    position: relative;
    margin: 30px 0px 0px 0px;
    justify-content: center;
    width: 50%;
    margin-top: -30px;
    margin-left: -10px;
    animation: opacityFx 1.5s ease-in-out;
  }
  .detailsData {
    /* grid-template-rows: repeat(4, 1fr); */
    grid-template-columns: repeat(2, 1fr);
    scale: 0.8;
    top: -80px;
    left: 16%;
    margin-bottom: -60px;
  }

  .parraf {
    height: auto;
  }

  .card {
    margin-top: 0px;
    scale: 0.7;
    margin-bottom: 0px;
    margin-left: 100px;
  }

  .leftArrowModal {
    /* position: absolute; */
    scale: 0.7;
    margin-left: 70px;
  }
  .rightArrowModal {
    /* position: absolute; */
    scale: 0.7;
    margin-left: 285px;
    margin-top: 50%;
  }

  .closeButtonModal {
    scale: 0.7;
  }

  .waitingBallPic {
    margin-left: 10px;
    margin-top: -70px;
    margin-bottom: 560px;
  }

  .closeButtonModal {
    top: 10px;
    right: 10px;
    scale: 0.6;
  }
}

@media only screen and (max-width: 559px) {
  .allDetailCard {
    width: 90%;
