  }
  .detailsData {
    min-height: 281px;
    max-height: 281px;
    left: 19%;
    margin-top: -30px;
  }
  .parraf {
    background-color: transparent;
  }

  .cardArrowContainer {
    top: 0px;
    left: -5%;
  }
  .card {
    margin-left: 65px;
  }
}

.card {
  margin-left: 70px;
